.container {
  margin-top: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
}
@media only screen and (max-width: 600px) {
  .container {
    margin-top: 0px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-start;
  }
}
